.main-page {
  background-color: #0e0e0c;
  color: #ccc5b9;
}
.showcase {
  padding: 50px 0;
  display: flex;
  align-items: center;
  .showcase-inside {
    height: 430px;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    .showcase-text {
      display: grid;
      align-items: center;
      color: #fff;
      .learn-more {
        text-decoration: none;
        font-size: 30px;
        .span-animate {
          vertical-align: bottom;
          display: inline-block;
          animation-name: animation;
          animation-duration: 2s;
          animation-iteration-count: infinite;
          animation-direction: alternate;
        }
      }
    }
    .hero {
      background-image: url("../images/20200418_130616-removebg-preview.png");
      background-repeat: no-repeat;
      background-position-x: center;
      filter: grayscale(0.6);
      height: 430px;
    }
  }
}
.active {
  color: #f0860c !important;
}
.my-navbar {
  height: 44px;
  padding-top: 10px;
  padding-bottom: 10px;
  background: linear-gradient(to bottom, #0e0e0c, #141413);

  .my-navbar-inside {
    display: flex;
    justify-content: space-between;

    .navbar-link {
      text-decoration: none;
      color: #fff;
      font-weight: 600;
    }
    .left-side .navbar-link:not(:first-child) {
      margin-left: 30px;
    }
  }
}

.footer {
  height: 44px;
  padding-top: 10px;
  padding-bottom: 10px;
  background: linear-gradient(to bottom, #11110f, #141413);
  position: fixed;
  bottom: 0;
  p {
    margin: 0;
  }
}
@keyframes animation {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(10px);
  }
}

.video {
  width: 100%;
  margin-top: 100px;
  height: 500px;
  .video-wrapper {
    width: 80%;
    margin: 0 auto;
    height: 100%;
    iframe {
      height: 100%;
      width: 100%;
    }
  }
}

.title {
  text-align: center;
}

.about-page {
  height: 800px;
}
.work-page {
  min-height: 99.9vh;
}
.contact-page {
  min-height: 99.9vh;
  text-align: center;
  .numbers {
    margin-top: 50px;
    font-size: 1.5rem;
    a {
      color: #ccc5b9;
      text-decoration: none;
      margin-top: 10px;
      display: inline-block;
      margin-left: 10px;
    }
  }
}
.sites-portfolio {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;

  .h100img {
    a {
      display: inline-block;
      height: 100%;
      width: 100%;
      img {
        height: 100%;
        width: 100%;
      }
    }
  }
  a {
    transition: 0.5s;
    &:hover {
      filter: brightness(0.4);
    }
  }
  img {
    max-width: 100%;
    max-height: 100%;
  }
}
@media (max-width: 992px) {
  .showcase .showcase-inside {
    grid-template-columns: repeat(1, 1fr);
  }
  .showcase .showcase-inside .showcase-text {
    order: 2;
  }
  .sites-portfolio {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 576px) {
  .sites-portfolio {
    grid-template-columns: repeat(1, 1fr);
  }
}
@media (max-width: 440px) {
  .contact-page .numbers {
    font-size: 1rem;
  }
}
